.maincontainer {
  /* height: 100vh; */
  width: 100vw;
  display: flex;
  justify-content: center;
}

p {
  font-size: 1.1rem;
}

h3, h4 {
  font-family: 'Arial'
}

input {
  border-radius: 3.5px;
  margin-bottom: 2.5px;
  padding-top: 3.5px;
  padding-bottom: 3.5px;
  text-align: center;
  border-width: 1px;
}

input:focus {
  outline: none;
  border-color: rgb(200, 200, 200);
  border-style: solid;
  border-width: 1px;
}

.containerforconfirmbtn {
  width: 100%;
  display: flex;
  justify-content: center;
}

button {
  border-color: #6778D6;
  background-color: #6778D6;
  color: white;
  border-style: solid;
  border-radius: 3px;
}